const jsonServerPath = 'https://chocopic.github.io/testDB/index.json';
const jsonLocalPath = '/testData.json';

//json-server 
export async function fetchData(){
    try{
        const response = await fetch(jsonServerPath);
        const data = await response.json();
        return data;
    }catch(error){
        console.log('  ', error);
    }
}