export function login(id, pw) {
    return new Promise((res, rej) => {
      return setTimeout(() => {
        sessionStorage.setItem("isAuth", "true");
        return res({ resultcode: 1 });
      }, 300);
    });
  }
//  
export function logout(){
    sessionStorage.setItem("isAuth", "false");
}


